import React from 'react';
import "../App.css";
import { Container, Row, Col } from "react-bootstrap";
import g from "../ima/gen.jpg"; import g1 from "../ima/compu.jpeg";
import pascal from "../ima/maquina_de_pascal.jpg"; import calculo from "../ima/Máquina-de-cálculo-diferencial.jpg";

const Historia = () => {
    return (
        <div className="fondoHist">
            <Container className="infoHist">
                <Row >
                    <Col  xs={12} md={6} xl={7} center>
                        <div>
                        <h1>HISTORIA</h1>
                        <p>
                            En la computación, las “generaciones de computadoras” es la forma de referirse a las etapas que definen los avances más significativos en la evolución de la informática y la tecnología en general. Se debe recordar que las computadoras no siempre han sido tan compactas y potentes como las que conocemos hoy en día, sino que a medida que la informática fue avanzando, fue posible usar todos esos conocimientos con el fin de crear computadoras más complejas y eficientes.
                        </p>
                        <p>
                            Algunos de los criterios que permitieron distinguir el paso de las generaciones de computadoras, fue la invención e implementación de nuevas técnicas que hicieron posible disminuir el tamaño de los componentes de los ordenadores, reducir los costos de producción y aumentar la potencia de los mismos. De esta manera, se dice que el paso de las generaciones computacionales han sido marcadas por las mejoras de la tecnología desde un punto de vista práctico, económico y de utilidad.
                        </p>
                        <p>
                            Actualmente se reconocen cinco generaciones de computadoras, donde cada una comprende un período de tiempo determinado. No obstante, muchos expertos apuntan a que la sexta generación se encuentra actualmente en estudio y desarrollo.
                            Cada una de las generaciones se caracterizaron por tener un desarrollo a nivel tecnológico muy importante; lo que marcó un punto y aparte en la historia de los ordenadores. La mayoría de estos avances desde 1940, se han enfocado en ofrecer ordenadores cada vez más compactos, baratos y potentes. Aquí te explico el resumen y logro más destacable de cada generación de ordenadores.
                        </p>
                        </div>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <div className="imgHist"> 

                        </div>
                        <img src={g} alt="Gen Ima" /><br /><br />
                        <img src={g1} alt="Gen" />
                    </Col>
                </Row>
            </Container>
            <Container className="infoHist parte2">
                <h2>Antecedentes a Generaciones de Computadoras</h2>
                <p>Los antecedentes de las computadoras, datan desde los años 4000 a.c. Se remontan a las primeras reglas de cálculo y las primeras máquinas diseñadas por humanos para facilitar la aritmética. El ábaco fue uno de esos instrumentos, que funcionó durante mucho tiempo como herramienta de cálculo simple; y sentó bases para la evolución de lo que hoy conocemos como ordenadores.</p>
                <img src={pascal} alt="Maquina de Pascal"/>
                <p>Pero fue un proceso bastante largo y complejo; en el que posteriormente se le unieron otros inventos como la popular máquina de Blaise Pascal, también conocida como Máquina de Pascal o Pascalina. Fue creada en 1642, pero tomó muchas décadas perfeccionarla. Consistía en una serie de diversos engranajes que permitían realizar operaciones aritméticas.</p>
                <p>Aún no era totalmente eficiente y tuvo distintas modificaciones. Eso hasta que en 1671 Gottfried Leibinitz mejoró y dio inicio a las primeras calculadoras, primas muy cercanas de las computadoras. Este sistema sirvió como fuerte a los intentos del ser humano para automatizar el conteo aritmético y la resolución de problemas.</p>
                <img src={calculo} alt="Calculo DIferencial"/>
                <p>En el año 1802, Joseph Marie Jacquard inventa un sistema de tarjetas perforadas para automatizar el trabajo de sus telares. Y 2 décadas más tarde, en 1822 Charles Babbage utiliza este mismo sistema para crear la primera máquina de cálculo diferencial. Lo que significó un salto muy grande.</p>
                <p>Tan solo 12 años más tarde, en 1834, Babbage fue capaz de perfeccionar su máquina de tal manera que podía realizar las 4 operaciones aritméticas; además de almacenar números en una especie de memoria. Esta Máquina Analítica representó un salto importante hacia la informática, y fue lo que otorgó el apodo del “Padre de la computación“. Aunque las primeras computadoras aparecerían un siglo más adelante.</p>
            </Container>
        </div>
    );
}

export default Historia;