import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/header.png";
import Carousel from "react-bootstrap/Carousel";
import g1 from "../ima/gen1.jpg";
import g2 from "../ima/gen2.jpg";
import g3 from "../ima/tercera_gen_cdc.jpg";
import g4 from "../ima/cuarta_gen_imbpc.jpg";
import g5 from "../ima/quinta_ gen_compu.jpeg";
import "../App.css";
import TrackVisibility from "react-on-screen";

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState("");
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const [index, setIndex] = useState(1);
    const toRotate = ["Computadoras"];
    const period = 2000;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta);

        return () => {
            clearInterval(ticker);
        };
    }, [text]);

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting
            ? fullText.substring(0, text.length - 1)
            : fullText.substring(0, text.length + 1);

        setText(updatedText);

        if (isDeleting) {
            setDelta((prevDelta) => prevDelta / 2);
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true);
            setIndex((prevIndex) => prevIndex - 1);
            setDelta(period);
        } else if (isDeleting && updatedText === "") {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setIndex(1);
            setDelta(500);
        } else {
            setIndex((prevIndex) => prevIndex + 1);
        }
    };

    return (

        <section className="banner" id="inicio">
            <Container>
                <Row className="info align-items-right">
                    <Col xs={12} md={6} xl={7}>
                        <TrackVisibility>
                            {({ isVisible }) => (
                                <div
                                    className={
                                        isVisible ? "animate_animated animate_fadeIn " : ""
                                    }
                                >
                                    <span className="tagline">Bienvenidos</span>
                                    <br />
                                    <h1>
                                        {'Generaciones'} <br />
                                        <span
                                            className="txt-rotate"
                                            dataPeriod="1000"
                                            data-rotate='["Computadoras" ]'
                                        >
                                            <span className="wrap">{text}</span>
                                        </span>
                                    </h1>
                                    <p>
                                        En la computación, las “generaciones de computadoras” es la
                                        forma de referirse a las etapas que definen los avances más
                                        significativos en la evolución de la informática y la
                                        tecnología en general. Se debe recordar que las computadoras
                                        no siempre han sido tan compactas y potentes como las que
                                        conocemos hoy en día, sino que a medida que la informática
                                        fue avanzando, fue posible usar todos esos conocimientos con
                                        el fin de crear computadoras más complejas y eficientes.
                                    </p>
                                </div>
                            )}
                        </TrackVisibility>
                    </Col>
                    <Col xs={12} md={6} xl={5}>
                        <TrackVisibility>
                            {({ isVisible }) => (
                                <div
                                    className={
                                        isVisible ? "animate_animated animate_fadeIn animacion" : ""
                                    }
                                >
                                    <img src={headerImg} alt="Header Img" />
                                </div>
                            )}
                        </TrackVisibility>
                    </Col>
                </Row>
            </Container>

            <div className="mensaje">
                <h3>
                    "Una computadora es para mí la herramienta más sorprendente que
                    hayamos ideado. Es el equivalente a una bicicleta para nuestras
                    mentes"
                </h3>
            </div>

            <h3>Existen 5 Generaciones de computadoras</h3>
            <div className="carrusel container-fluid">
                <Carousel data-bs-theme="dark">
                    <Carousel.Item>
                        <img class="d-block mx-auto w-auto h-auto" src={g1} alt="G1 Ima" />
                        <Carousel.Caption>
                            <h5>Primera generación</h5>
                            <p>1940 hasta 1956</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={g2} alt="G1 Ima" class="d-block mx-auto w-auto h-auto" />
                        <Carousel.Caption>
                            <h5>Segunda generación</h5>
                            <p>1956 hasta 1964</p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={g3} alt="G1 Ima" class="d-block mx-auto w-auto h-auto" />
                        <Carousel.Caption>
                            <h5>Tercera generación</h5>
                            <p>
                                1965 hasta 1971
                            </p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={g4} alt="G1 Ima" class="d-block mx-auto w-auto h-auto" />
                        <Carousel.Caption>
                            <h5>Cuarta generación</h5>
                            <p>
                                1972 hasta 1980
                            </p>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={g5} alt="G1 Ima" class="d-block mx-auto w-50 h-50" />
                        <Carousel.Caption>
                            <h5>Quinta generación</h5>
                            <p>
                                1983 hasta la fecha
                            </p>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>
            </div>

        </section>

    );
};