import React from 'react'
import "../App.css";
import { Container, Row, Col } from "react-bootstrap";
import gen1 from "../ima/primera_gen1.jpg";
import eniac1 from "../ima/primera_gen1_eniac.jpg";

export const Gen1 = () => {
    return (
        
        <div className="cajaGen">
            <h1>PRIMERA GENERACIÓN 1940 - 1956</h1>
            <p>
                La historia de las generaciones de computadoras, inicia con la invención del primer computador. Su autoría no puede atribuirse a una única persona. Si bien, Babbage es conocido como el padre de las primeras ramas para los saberes de la computación; no fue sino hasta años después que tuvimos las primeras máquinas propiamente dichas.
                En el proceso, también influyeron importantes figuras como Alan Turing con su Máquina de Turing o Máquina universal. Al final, la mayor característica de los primeros computadores era la capacidad de hacer cálculos de forma automática y digital, por la necesidad de resolver códigos secretos enemigos durante la segunda guerra mundial.
            </p>
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7} center>
                        <h2>Características</h2>
                        <ul>
                            <li>Necesitaban el uso de tarjetas perforadas para ingresar datos y realizar la programación. Dichas tarjetas perforadas eran la manera de indicar las instrucciones en código binario (lenguaje de máquina) al equipo.</li>
                            <li>Los primeros equipos informáticos eran tan grandes que abarcaban varios metros cúbicos. Estos se valían de tubos al vacío para los circuitos y tambores magnéticos para la memoria.</li>
                            <li>Estas generaciones de las computadoras eran programadas en lenguaje de máquina, el lenguaje de más bajo nivel. Al ser algo nuevo, suponía un desafío para los primeros usuarios en operarla.</li>
                            <li>Muchas de sus instrucciones, muy simples y específicas, eran suministradas a través de tarjetas perforadas de papel o cartón. Mientras la salida era mostrada en impresiones.</li>
                            <li>Solo podían resolver un problema a la vez, y a los operadores muchas veces les tomaba días o hasta semanas, establecer un nuevo problema.</li>
                        </ul>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <img src={gen1} alt="G1" /><br /><br />
                        <img src={eniac1} alt="G1 ENIAC" />
                    </Col>
                </Row>
            </Container>
            <Container >
                    <div className="ratio ratio-16x9 ">
                        <iframe className="video"
                        src="https://www.youtube.com/embed/AuMVvq-h5Mo?rel=0"
                            title="YouTube video"
                            allowfullscreen
                        ></iframe>
                    </div>
            </Container>
        </div>
    )
}

