import React from 'react'
import { Container, Row, Col } from "react-bootstrap";
import gen3 from "../ima/tercera_gen_dec.jpg";
import cdc from "../ima/tercera_gen_cdc.jpg";

export const Gen3 = () => {
    return (
        <div className=" cajaGen cajaGen3">
            <h1>TERCERA GENERACIÓN 1965 - 1971</h1>
            <p>
                La tercera generación estuvo vigente entre los años 1965 y 1971. La llegada de esta nueva era de la computación estuvo marcada por la invención de los circuitos integrados, lo que revolucionó por completo la capacidad de procesamiento que podía llegar a tener un ordenador. El uso de este tipo de circuitos también hizo posible reducir de manera importante el tamaño de las PC.
                La llegada de la tercera generación dio paso a la electrónica tal y como la conocemos hoy, y es que gracias a las pastillas de silicio, fue posible integrar un gran número de piezas electrónicas en un componente único y miniatura. Igualmente, esta revolución tecnológica hizo posible disminuir el consumo energético de las computadoras.
            </p>
            <p>Tal era la eficiencia de las nuevas computadoras de tercera generación, que algunas eran capaces de realizar operaciones tan complejas como calcular el valor de π (Pi) con un rango de hasta 500 decimales.</p>
            <p>Fue durante esta época que surgió uno de los primeros dispositivos externos de almacenamiento, y es que fue durante la tercera generación de computadoras que se inventaron los discos magnéticos y componentes electrónicos de una sola pieza. Hay que destacar también que en esta época las computadoras volvieron a reducir de manera considerable su precio de fabricación, lo que hizo posible que cada vez más personas tuvieran acceso a este tipo de tecnología.</p>
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7} center>
                        <h2>Características</h2>
                        <ul>
                            <li>Los ordenadores de tercera generación fueron los primeros en utilizar circuitos integrados para llevar a cabo el procesamiento de información.</li>
                            <li>Gracias a la creación de los chips, fue posible procesar y almacenar información dentro de las computadoras de forma más eficiente. Además, los chips (pequeñas pastillas de silicio) eran mucho más económicas y fáciles de fabricar.</li>
                            <li>Durante esta época apareció la multiprogramación.</li>
                            <li>Gracias a las nuevas posibilidades que brindaba la computación y el uso de los lenguajes de programación de alto nivel, empezaron a explotar la industria de creación de software.</li>
                            <li>El uso de los nuevos componentes electrónicos permitió reducir aún más el tamaño y peso de las computadoras.</li>
                            <li>Los equipos de tercera generación no necesitaban de piezas electro-mecánicas, lo que redujo de manera significativa la generación de calor y el consumo de energía eléctrica.</li>
                        </ul>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <img src={gen3} alt="G3 DEC PDP" /><br /><br />
                        <img src={cdc} alt="G3 CDC 6600" />
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

