import React from 'react'
import { Container, Row, Col } from "react-bootstrap";
import mac from "../ima/quinta_ gen_macbook.jpg";
import gen5 from "../ima/quinta_ gen_compu.jpeg";

export const Gen5 = () => {
    return (
        <div className=" cajaGen cajaGen5">
            <h1>TERCERA GENERACIÓN 1983 - ACTUALIDAD</h1>
            <p>
                La quinta generación comenzó en 1983 y sigue vigente en la actualidad. Con el perfeccionamiento del uso de los microprocesadores y la llegada de nuevas tecnologías de hardware, las computadoras pasaron a ser equipos de uso portátil, livianos y sencillos de utilizar. Fue a partir de este período que surgieron las computadoras portátiles o laptops, lo que revolucionó la concepción del uso de la computadora.
            </p>
            <p>Otro avance significativo que tuvo lugar con la llegada de la quinta generación fue la llegada del internet, un medio que permitió conectar las computadoras a través de una red sin importar la distancia entre cada ordenador. Gracias a esto, la comunidad informática se enriqueció en gran medida, dando paso a que se crearan grupos de programadores que trabajaban de manera conjunta para ofrecer soporte y creación de softwares de manera conjunta.</p>
            <p>Es a partir de esta generación que se asienta la idea de que un ordenador debe ser fácil de utilizar, intuitivo y útil para cumplir con todo tipo de propósitos: desde fines científicos y administrativos, hasta de entretenimiento y desarrollo.</p>
            <p>Durante la década de los años 80, Japón lideró un proyecto enfocado en crear un computador cuya tecnología fuera capaz de funcionar como una inteligencia artificial, sin embargo, al no obtener resultados significativos, el proyecto fue abandonado. No obstante, actualmente es cada vez más evidente que esta meta está más cerca de ser lograda.</p>
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7} center>
                        <h2>Características</h2>
                        <ul>
                            <li>A partir de la llegada de la quinta generación empieza el boom de las computadoras económicas, eficientes y portátiles.</li>
                            <li>Es a partir de 1983 que aparece la tecnología que utiliza los materiales superconductores, lo que permitió el desarrollo de microprocesadores cada vez más potentes.</li>
                            <li>Comienza el estudio y desarrollo de la inteligencia artificial.</li>
                            <li>El uso de interfaces amigables para cualquier usuario se vuelve una tendencia</li>
                            <li>Surgen lenguajes de programación de alto nivel cada vez más completos.</li>
                            <li>Empieza a ganar popularidad el uso de los ordenadores con fines recreativos.</li>
                            <li>Con la llegada de la quinta generación de computadoras, empieza a implementarse el procesamiento paralelo, lo que permitió hacer que el procesamiento de datos fuera cada vez más veloz y eficiente.</li>
                            <li>Nace el sistema operativo Microsoft Windows, uno de los más populares de todos los tiempos.</li>
                        </ul>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <img src={mac} alt="G5 MACBOOK  " />
                        <img src={gen5} alt="G5 COMPUTADORA " />
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

