import React from 'react'
import { Container, Row, Col } from "react-bootstrap";
import gen4 from "../ima/cuarta_gen_macintosh.jpg";
import ibmpc from "../ima/cuarta_gen_imbpc.jpg";

export const Gen4 = () => {
    return (
        <div className=" cajaGen cajaGen4">
            <h1>CUARTA GENERACIÓN 1972 - 1980</h1>
            <p>
                La llegada de la cuarta generación se dio en 1972 y estuvo vigente hasta el año 1980. Gracias a los avances en la electrónica, se inventó el microprocesador, un potente circuito integrado capaz de contener todos los elementos indispensables que necesita un ordenador para realizar tareas y contener datos fundamentales para su funcionamiento. Esta nueva revolución tecnológica dio paso a que un computador no necesitara de anillos de silicio como memoria, ya que en su lugar, utilizaba los nuevos circuitos integrados que permitían diversificar y almacenar procesos lógicos y aritméticos con múltiples propósitos.
            </p>
            <p>De este avance de la informática surgió el término PC, y es que la llegada de las computadoras personales era una realidad gracias a los microprocesadores. También, es durante esta generación que llega al mercado el disquete, un dispositivo magnético de almacenamiento externo que permitía almacenar información que podría reconocer cualquier ordenador en el que éste fuera insertado.</p>
            <p>La era del microprocesador permitió abaratar mucho más los costos de producción de computadoras, ya que además de toda la potencia de procesamiento que brindaban, eran mucho más baratos de fabricar. Además de eso, se daba paso a la posibilidad de desarrollar nuevos softwares de sistema y de aplicación que ayudarían a las personas a familiarizarse aún más con la idea de sacar provecho de una computadora de uso personal.</p>
            <Container>
                <Row>
                    <Col xs={12} md={6} xl={7} center>
                        <h2>Características</h2>
                        <ul>
                            <li>Tuvo lugar uno de los más importantes inventos de la electrónica: el microprocesador.</li>
                            <li>Fue durante esta era que se empezaron a construir las microcomputadoras, lo que hoy conocemos como Computadoras o PC, es decir, ordenadores de uso particular para cualquier persona.</li>
                            <li>Los avances en el uso de los chips dieron paso al desarrollo de supercomputadoras, equipos increíblemente avanzados capaces de manejar metadatos y un gran número de variables, lo que impulsó aún más el progreso del estudio de ciencias como la física atómica.</li>
                            <li>Fue durante la cuarta generación que surgió el concepto de lo que sería el internet.</li>
                            <li>Las computadoras se hicieron mucho más económicas, silenciosas y eficientes.</li>
                            <li>Fueron los primeros ordenadores en utilizar periféricos como el Muose y el GUI.</li>
                        </ul>
                    </Col>
                    <Col xs={12} md={6} xl={5} center>
                        <img src={gen4} alt="G4 MACINTOSH" />
                        <img src={ibmpc} alt="G4 IBM PC" />
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

