import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import gen2 from "../ima/segunda_gen1_pdp.jpg";
import sys from "../ima/segunda_gen1_system360.jpg";

export const Gen2 = () => {
  return (
    <div className=" cajaGen cajaGen2">
      <h1>SEGUNDA GENERACIÓN 1956 - 1964</h1>
      <p>
        La segunda generación de computadoras inicia en el 1956 y se extiendo
        por pocos años hasta 1964. El cambio más interesante fue dado por la
        sustitución de tubos de vacío, los cuales fueron reemplazados por
        transistores. Estos aparatos son como interruptores eléctricos de
        materiales sólidos, fabricados por los laboratorios Bell en Estados
        Unidos en los años 1947. El transistor fue fundamental para comenzar a
        fabricar ordenadores más pequeños, baratos, rápidos, eficientes, con
        menor gasto energético y mucho más confiables. Esto también fue el
        inicio para elaborar los primeros microchip, que dio paso de los
        aparatos eléctricos a los electrónicos.
      </p>
      <Container>
        <Row>
          <Col xs={12} md={6} xl={7} center>
            <h2>Características</h2>
            <ul>
              <li>
                El transistor demostró ser superior a los tubos de vacío, por
                ende hubo una gran reducción de tamaño en las máquinas, que
                ahora consumen menos energía y producen menos calor.
              </li>
              <li>
                Se vio el nacimiento del microprocesador o microchip, que fue
                fundamental para la evolución de las siguientes generaciones de
                computadoras, así como de electrónica en general.
              </li>
              <li>
                Fueron las primeras máquinas en depender de un lenguaje de
                programación, más concretamente el lenguaje FORTRAN que apareció
                en 1953 y se hizo más conocido a partir de 1955. Aunque en 1959,
                la comercialización de COBOL se hizo tan famosa, que no pasó
                mucho tiempo antes de que las tarjetas perforadas quedaran
                obsoletas.
              </li>
              <li>
                Estos también fueron los primeros equipos en almacenar
                instrucciones en una memoria, las cuales pasaron de usar
                tambores magnéticos a tener una tecnología de núcleo magnético,
                con mayor capacidad.
              </li>
              <li>
                Los primeros ordenadores de esta generación, fueron
                desarrollados por la industria de la energía atómica.
              </li>
              <li>
                Su popularidad aumentó al grado de comercializarse en una mayor
                cantidad de sectores. Sobre todo en el de la banca, contabilidad
                y logística.
              </li>
            </ul>
          </Col>
          <Col xs={12} md={6} xl={5} center>
            <img src={gen2} alt="G2 PDP" />
            <br />
            <br />
            <img src={sys} alt="G2 SYSTEM 360" />
          </Col>
        </Row>
      </Container>
      <Container>

      <iframe
        className="video"
        src="https://www.youtube.com/embed/MukEdpELaMM?si=YTXsr8wmMUgYvQmT"
        title="YouTube video player"
        frameborder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowfullscreen
      ></iframe>
      </Container>
    </div>
  );
};
