import React from 'react'
import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { Form, Button } from "react-bootstrap";
import "../App.css";
import TrackVisibility from "react-on-screen";

export const Prueba = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState("");
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const [index, setIndex] = useState(1);
    const toRotate = ["Conocimiento"];
    const period = 2000;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta);

        return () => {
            clearInterval(ticker);
        };
    }, [text]);

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting
            ? fullText.substring(0, text.length - 1)
            : fullText.substring(0, text.length + 1);

        setText(updatedText);

        if (isDeleting) {
            setDelta((prevDelta) => prevDelta / 2);
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true);
            setIndex((prevIndex) => prevIndex - 1);
            setDelta(period);
        } else if (isDeleting && updatedText === "") {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setIndex(1);
            setDelta(500);
        } else {
            setIndex((prevIndex) => prevIndex + 1);
        }
    };

    return (

        <section className="banner cajaGen2" id="inicio">
            <Container>
                <Row className="info align-items-right">
                    <Col xs={12} md={6} xl={7}>
                        <TrackVisibility>
                            {({ isVisible }) => (
                                <div
                                    className={
                                        isVisible ? "animate_animated animate_fadeIn" : ""
                                    }
                                >
                                    <br />
                                    <h1>
                                        {'Pon a prueba tu'} <br />
                                        <span
                                            className="txt-rotate"
                                            dataPeriod="1000"
                                            data-rotate='["Conocimiento" ]'
                                        >
                                            <span className="wrap">{text}</span>
                                        </span>
                                    </h1>
                                    <p>
                                        Con la información obtenida en la página pon a prueba tus conocimientos en la siguiente prueba. Primera llena el formulario con tus datos.
                                    </p>
                                </div>
                            )}
                        </TrackVisibility>
                    </Col>
                    <Col xs={12} md={6} xl={5}>
                        <TrackVisibility>
                            <Form>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>Nombre</Form.Label>
                                    <Form.Control type="text" placeholder="Escribe tu nombre" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Label>Apellido</Form.Label>
                                    <Form.Control type="text" placeholder="Escribe tu apellido" />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Label>Nickname</Form.Label>
                                    <Form.Control type="text" placeholder="Escribe tu nick" />
                                </Form.Group>
                                <Button variant="primary" type="submit">
                                    Enviar
                                </Button>
                            </Form>
                        </TrackVisibility>
                    </Col>
                </Row>
            </Container>
        </section>
    );
};