import React, { useState, useEffect } from 'react';
import { Navbar, Nav, Container, NavDropdown, Form, FormControl, Button } from 'react-bootstrap';
import { BrowserRouter as Router, Link } from 'react-router-dom';

export const NavBar = () => {
    const [activeLink, setActiveLink] = useState('inicio');
    const [scrolled, setScrolled] = useState(false);
    useEffect(() => {
        const onScroll = () => {
            if (window.scrollY > 50) {
                setScrolled(true);
            } else {
                setScrolled(false);
            }
        }
        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    }, [])

    const onUpdateActiveLink = (value) => {
        setActiveLink(value);
    }

    return (
        <Router>
            <Navbar expand="lg" className={`bg-body-tertiary ${scrolled ? 'scrolled' : ''}`}>
                <Container fluid>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="ms-auto">
                            <Nav.Link as={Link} to="/" className={activeLink === 'inicio' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('inicio')}>Inicio</Nav.Link>
                            <Nav.Link as={Link} to="/historia" className={activeLink === 'historia' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('historia')}>Historia</Nav.Link>
                            <NavDropdown title="Generaciones" id="responsive-nav-dropdown">
                                <NavDropdown.Item as={Link} to="/gen1" className={activeLink === 'gen1' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('gen1')}>1ra Generación</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/gen2" className={activeLink === 'gen2' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('gen2')}>2da Generación</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/gen3" className={activeLink === 'gen3' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('gen3')}>3ra Generación</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/gen4" className={activeLink === 'gen4' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('gen4')}>4ta Generación</NavDropdown.Item>
                                <NavDropdown.Item as={Link} to="/gen5" className={activeLink === 'gen5' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('gen5')}>5ta Generación</NavDropdown.Item>
                            </NavDropdown>
                            <Nav.Link as={Link} to="/prueba" className={activeLink === 'prueba' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('prueba')}>Prueba</Nav.Link>
                            <Nav.Link as={Link} to="/creditos" className={activeLink === 'creditos' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('creditos')}>Créditos</Nav.Link>
                        </Nav>
                        <Form className="d-flex">
                            <FormControl
                                type="search"
                                placeholder="Search"
                                className="me-2"
                                aria-label="Search"
                            />
                            <Button variant="outline-success">Buscar</Button>
                        </Form>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </Router>
    );
}